@keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.frameChild {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-firebrick);
  position: absolute;
  top: 105px;
  left: 140px;
  width: 89px;
  height: 29px;
  overflow: hidden;
}
.frameChild:hover {
  animation: 5s ease 0s infinite normal none rotate-center;
  opacity: 1;
}
